.testimonials {
    padding-top: 97px;
    background-color: var(--color-athems);

    &__content {
        max-width: 612px;
        overflow: hidden;
        @include tablet {
            max-width: 450px;
        }

        @include mobile {
            width: 100%;
            max-width: none;
        }
    }

    &__image {
        position: relative;
        z-index: 5;
        margin-bottom: -60px;

        @include minidesk {
           width: 380px;
           object-fit: cover;
        }
        @include tablet {
            width: 300px;
        }
        @include minitablet {
            display: none;
        }

    }

    &__title {
        margin-bottom: 60px;
    }

    &__items {
        position: relative;
        padding-bottom: 12px;
    }

    &__slider-nav {
      position: absolute;
      right: 0;
      bottom: 0;
    }

    &__container {
        display: flex;
        justify-content: space-between;
    }

    @include minitablet {
        padding-top: 40px;
        padding-bottom: 40px;
    }

}

.testimonials-item {
   &__image {
       width: 72px;
       height: 72px;
       overflow: hidden;
       border-radius: 100%;
       margin-bottom: 25px;

       img {
           display: block;
           width: 100%;
           height: 100%;
           object-fit: cover;
       }
   }

   &__descr {
       margin: 0;
       margin-bottom: 25px;
       font-size: 16px;
       line-height: 160%;
       color: var(--color-gray);
   }

   &__author {
       display: block;
       font-weight: 700;
       font-size: 16px;
       line-height: 160%;
       color: var(--color-ebony);
       margin-bottom: 4px;
       padding-right: 110px;
   }

   &__author-position {
       display: block;
       font-size: 14px;
       line-height: 150%;
       color: var(--color-storm);
       padding-right: 110px;
   }
}