.page-hero {
    padding-top: 153px;
    padding-bottom: 80px;
    background-color: var(--color-athems);
     min-height: 600px;
     background-position: right -8px top;
    background-repeat: no-repeat;

    &__breadcrumbs {
        margin-bottom: 40px;
    }

    &__title {
        margin: 0;
        max-width: 600px;
        font-weight: 700;
        font-size: 72px;
        line-height: 130%;
        letter-spacing: 1px;
        color: var(--color-ebony);
        text-transform: uppercase;
        margin-bottom: 24px;

        &--small {
            font-size: 64px;
            line-height: 120%;
            letter-spacing: 1px;
            text-transform: initial;

        }

        &--mb {
            margin-bottom: 60px;
        }

        @include mobile {
            font-size: 40px;
            line-height: 54px;
        }

        
    }
    
    &__descr  {
        margin: 0;
        max-width: 600px;
        font-weight: normal;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-storm);

        @include mobile {
            font-size: 16px;
            line-height: 24px;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;

        @include mobile {
            flex-direction: column;
            align-items: flex-start;
          }
    }

    &__meta {
        .article-meta__item {
            font-size: 16px;
            line-height: 160%;
        }

        a.article-meta__item {
            font-weight: 700;
            color: var(--color-primary); 
        }

        time.article-meta__item {
            padding-left: 28px;
              background-image: url("../img/clock.svg");
              background-position: left center;
              background-size: 20px 20px;
              background-repeat: no-repeat;
        }

        article-meta__item--comments {
            background-size: 20px 20px;
        }

        .article-meta__item:not(:last-child)::after {
            height: 16px;
            right: -17px;
        }

        .article-meta__item:not(:last-child) {
            margin-right: 20px;
        }
    }


    &__social {
        svg {
            fill: var(--color-storm);
            opacity: 1;
        }
    }

    &--services {
    background-image: url("../img/services-hero-bg.png");
  
    }

    &--services-inner {
        background-image: url("../img/services-hero-inner-bg.png");
       
    }

    &--work {
            background-image: url("../img/work-hero-bg.png");
    }

    &--work-inner {
        background-color: var(--color-light);
        background-position: right -8px top 48px;
        background-image: url("../img/hero-work-inner-bg.png");
}

      &--about {
    // background-color: var(--color-light);
    // background-position: right -8px top 48px;
    background-image: url("../img/about-hero-bg.png");
    }



    &--positions {
        background-position: right -9px bottom;
        background-image: url("../img/hero-positions-bg.png");
    }

    &--news {
        background-position: right -9px bottom;
        background-image: url("../img/news-hero-bg.png");
    }

    
    &--post {

        .page-hero__title {
          margin-bottom: 48px;
          max-width: none;
          font-size: 64px;
          line-height: 120%;
          letter-spacing: 1px;
          text-transform: initial;

          @include minitablet {
            font-size: 45px;
            line-height: 58px;
          }

          @include mobile {
            font-size: 35px;
            line-height: 48px;
          }
        }

        @include minidesk {
           margin-bottom: 30px;
          }
        
    }

    &--contacts {
        background-position: right -9px bottom;
        background-image: url("../img/contacts-hero-bg.png");
    }

    @include tablet {
        min-height: auto;
       background-image: none;
    }

    
       


}